import React from "react";
import Breadcrumb from "../Breadcrumb";
import { Container, Row, Col, Table } from "react-bootstrap";
import { Link } from "react-router-dom";
import { usePDF } from 'react-to-pdf';
import favicon from '../../../assets/images/favicon.svg'

export default function Invoice() {

  const { toPDF, targetRef } = usePDF({filename: 'page.pdf'});

  const invoiceData = {
    invoiceNumber: "12345",
    date: "10 Jan 2025",
    from: {
      name: "Your Company Name",
      address: "123 Business Street, City, State, ZIP",
      email: "info@yourcompany.com",
    },
    to: {
      name: "Client Name",
      address: "456 Client Avenue, City, State, ZIP",
      email: "client@example.com",
    },
    items: [
      { description: "Product 1", quantity: 2, unitPrice: 50.0, total: 100.0 },
      { description: "Service A", quantity: 1, unitPrice: 75.0, total: 75.0 },
    ],
    taxRate: 0.1,
  };

  const calculateSubtotal = () =>
    invoiceData.items.reduce((sum, item) => sum + item.total, 0);

  const calculateTax = (subtotal) => subtotal * invoiceData.taxRate;

  const calculateTotal = (subtotal, tax) => subtotal + tax;

  const subtotal = calculateSubtotal();
  const tax = calculateTax(subtotal);
  const total = calculateTotal(subtotal, tax);
  return (
    <div>
      <div className="mainContent">
        <Breadcrumb />
        <div className="mainContent__assets">
          <Container>
            <Row>
              <Col lg={12}>
                {/* Account setting */}
                <div className="personalDetailsForm">
                  <div className="secIntro mb-0 align-items-center">
                    <div className="secIntro__left">
                      <h2 className="mb-0">Tax Invoice</h2>
                    </div>
                    <button className="btn btn-teal ms-auto rounded-2" onClick={() => toPDF()} style={{padding:'16px 35px'}}>
                      DOWNLOAD INVOICE
                    </button>
                  </div>

                  <div className="invoice"
                    style={{
                      width: "100%",
                      margin: "20px auto",
                      fontFamily: "Arial, sans-serif",
                      border: "1px solid #ddd",
                      padding: "20px",
                    }}
                    ref={targetRef}
                  >

                    {/* logo */}
                    <div style={{width:'150px','textAlign':'center'}}>
                     <img src={favicon} style={{width:'100px'}}/>
                     <div style={{width:'100%'}}><a href="" style={{'color': '#4ab9ea'}}>LicenceNow.com.au</a></div>
                     </div>
                    {/* logo */}
                    {/* Header */}
                    <div style={{ textAlign: "left", marginBottom: "40px",marginTop:'50px' }}>
                      <h2 style={{ margin: 0,marginBottom:'20px' }}>Tax Invoice</h2>
                      <p style={{ margin: 0, fontSize: "14px", color: "#666" }}>
                        Invoice #{invoiceData.invoiceNumber}
                      </p>
                      <p style={{ margin: 0, fontSize: "14px", color: "#666" }}>
                        Date: {invoiceData.date}
                      </p>
                      <p style={{ margin: 0, fontSize: "14px", color: "#666" }}>Licence Now</p>
                      <p style={{ margin: 0, fontSize: "14px", color: "#666" }}>ABN 35 4789 403 049</p>
                      <p style={{ margin: 0, fontSize: "14px", color: "#666" }}>123 street address, city, state</p>
                      <p style={{ margin: 0, fontSize: "14px", color: "#666" }}> Postcode</p>
                      <p style={{ margin: 0, fontSize: "14px", color: "#666" }}>(08) 94445612</p>
                      <p style={{ margin: 0, fontSize: "14px", color: "#666" }}><a href="mailto:support@licencenow.au">support@licencenow.au</a></p>
                    </div>

                    {/* From and To Section */}
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "space-between",
                        marginBottom: "20px",
                      }}
                    >
                       <div style={{ width: "48%" }}>
                        <h4 style={{ marginBottom: "10px",'border-bottom':'1px solid #4ab9ea' }}>To</h4>
                        <p style={{ margin: 0, fontSize: "14px" }}>
                          <b>Michael smith</b>
                        </p>
                        <p style={{ margin: 0, fontSize: "14px" }}>
                        <b>Licence Now</b>
                        </p>
                        <p style={{ margin: 0, fontSize: "14px" }}>
                        <b>2 adara road Bullsbrook, WA</b>
                        </p>
                        <p style={{ margin: 0, fontSize: "14px" }}>
                        <b>Bullsbrook, WA 6084</b>
                        </p>
                         
                      </div>
                      <div style={{ width: "48%" }}>
                        <h4 style={{ marginBottom: "10px",'border-bottom':'1px solid #4ab9ea' }}>&nbsp;</h4>
                        <p style={{ margin: 0, fontSize: "14px" }}>
                        <b>Date</b>: 20th july 2023
                        </p>
                        <p style={{ margin: 0, fontSize: "14px" }}>
                        <b>ID</b>: 45654123342
                        </p>
                        <p style={{ margin: 0, fontSize: "14px" }}>
                        <b>Amount</b>: $1,230.39
                        </p>
                        <p style={{ margin: 0, fontSize: "14px" }}>
                        <b>Description</b>: LN Transfer
                        </p>
                      </div>
                    </div>

                    {/* Invoice Table */}
                    <table
                      style={{
                        width: "100%",
                        borderCollapse: "collapse",
                       
                      }}
                    >
                      <thead>
                        <tr style={{ backgroundColor: "#dbd8d8" }}>
                          <th
                            style={{
                              border: "1px solid #ddd",
                              padding: "10px",
                              textAlign: "left",
                            }}
                          >
                            Date
                          </th>
                          <th
                            style={{
                              border: "1px solid #ddd",
                              padding: "10px",
                              textAlign: "center",
                            }}
                          >
                            Description
                          </th>
                          <th
                            style={{
                              border: "1px solid #ddd",
                              padding: "10px",
                              textAlign: "center",
                            }}
                          >
                            GST
                          </th>
                          <th
                            style={{
                              border: "1px solid #ddd",
                              padding: "10px",
                              textAlign: "right",
                            }}
                          >
                            SERVICE FEE
                          </th>
                        </tr>
                      </thead>
                      <tbody>
                        {invoiceData.items.map((item, index) => (
                          <tr key={index} style={{'background-color': index%2 === 1 ? '#e5e4e4': '#ffffff'}}>
                            <td
                              style={{
                                border: "1px solid #ddd",
                                padding: "10px",
                              }}
                            >
                              20th july 2023
                            </td>
                            <td
                              style={{
                                border: "1px solid #ddd",
                                padding: "10px",
                                textAlign: "center",
                              }}
                            >
                              #6782903 - Service Fee
                              <p>Driving lesson (2hr @$75.00)</p>
                            </td>
                            <td
                              style={{
                                border: "1px solid #ddd",
                                padding: "10px",
                                textAlign: "center",
                              }}
                            >
                              ${item.unitPrice.toFixed(2)}
                            </td>
                            <td
                              style={{
                                border: "1px solid #ddd",
                                padding: "10px",
                                textAlign: "right",
                              }}
                            >
                              ${item.total.toFixed(2)}
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>

                    {/* Total Section */}
                    <div
                      style={{ display: "flex", justifyContent: "flex-end" }}
                    >
                      <table
                        style={{ width: "300px", borderCollapse: "collapse" }}
                      >
                        <tbody>
                          <tr>
                            <td
                              style={{
                                padding: "10px",
                                border: "1px solid #ddd",
                                textAlign: "left",
                              }}
                            >
                              GST Amount
                            </td>
                            <td
                              style={{
                                padding: "10px",
                                border: "1px solid #ddd",
                                textAlign: "right",
                              }}
                            >
                              ${subtotal.toFixed(2)}
                            </td>
                          </tr>
                          <tr>
                            <td
                              style={{
                                padding: "10px",
                                border: "1px solid #ddd",
                                textAlign: "left",
                              }}
                            >
                              Sub Total
                            </td>
                            <td
                              style={{
                                padding: "10px",
                                border: "1px solid #ddd",
                                textAlign: "right",
                              }}
                            >
                              ${tax.toFixed(2)}
                            </td>
                          </tr>
                          
                        </tbody>
                      </table>
                    </div>

                    {/* Footer */}
                    <div style={{ textAlign: "center", marginTop: "20px" }}>
                      <p style={{ fontSize: "14px", color: "#666" }}>
                        Thank you for your business!
                      </p>
                      <p style={{ fontSize: "14px", color: "#666" }}>
                        Payment due within 30 days.
                      </p>
                    </div>
                  </div>
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
    </div>
  );
}
